.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
  animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.modal-inner-container {
  position: relative;
  width: 100%;
  height: 100%; }

.modal-title {
  font-size: 4vh;
  position: absolute;
  top: 10%;
  left: 10%;
  color: #908d81; }

.modal-text {
  font-size: 2.5vh;
  position: absolute;
  top: 25%;
  left: 10%;
  right: 10%;
  color: #d2cdbc; }

.modal-close-btn {
  font-size: 3vh;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  color: #908d81; }

.modal-overlay {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.75); }

.modal-content {
  position: absolute;
  top: 25%;
  left: 25%;
  right: 25%;
  bottom: 25%;
  background: #343635;
  overflow: auto;
  outline: none;
  padding: 2%;
  background-size: contain; }



